@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;1,500;1,600&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,100;1,500&display=swap');

/* font-family: 'Playfair Display', serif;
font-family: 'Roboto', sans-serif; */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: #120d0d;
}
html{
  font-size: 62.5%;
  overflow-x: hidden;
}
body{
  display:flex;
  align-items:center;
  justify-content:center;
  min-width: 100%;
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  color: rgb(240, 240, 240);
  background: rgba(0, 0, 0, 0.913);
}
h1,h2,h3,h4{
  font-family: 'Roboto', sans-serif;
  /* font-family: 'Playfair Display', serif; */
  font-weight: 700;
  font-size: 2.4rem;
  /* color: rgb(238, 238, 238); */
}
button{
  background-color: rgb(7, 133, 0);
  color: #d8d8d8;
  border: none;
  outline: none;
  padding: .6rem 1.2rem;
  font-size: 1.6rem;
  border-radius:4px;
  
}
.btn{
    margin: 2rem 0rem;
    weight: 150px;
    display: block;
    width: 100%;
    font-size:16px;
}
.Container{
  width:700px;
  height:70vh;
  background:#fff;
  color: #000;
  padding: 1rem 4rem;
  border-radius: 6px;
  text-align:center;
  display:flex;
  align-items:center;
  justify-content:center;
}
.form-container{
  display:flex;
  align-item:center;
  justify-content:center;
  gap:1rem;
  width:100%;
  height:auto;
}
.form{
  /* background:#88888880; */
  flex-bases:1;
  /* background:red; */
  weight:60%;
  /* opacity: .5; */
  display:flex;
  align-items:center;
  justify-content:center;
}
/* .form form button{
  weight:100%;
} */
.img{
  flex-bases:1;
  weight:40%;
  height:auto;
}
.img img{
  padding:1rem 2rem;
  width:100%;
  height:auto;
  object-fit:containe;
}
.input-group{
  text-align:left;
  margin: .8rem 0rem;
}
.label{
  font-size: 16px;
  color: black;
}
.input-group input{
  border:none;
  outline:none;
  padding: .4rem 1rem;
  background:#4c4c4c50;
  border-radius:4px;
  font-size: 16px;
}
.error{
  font-size:12px;
  color:red;
}